@import url(https://fonts.googleapis.com/css2?family=WindSong:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.gradient {
  background: linear-gradient(90deg, hsla(291, 79%, 51%, 1) 0%, hsla(240, 91%, 73%, 1) 100%);
  width: 100%;
  height: 100%;
}
.overlay {
    height: 100vh;
    width: 100%;
    padding: 0;
    margin: 0;
    background-color: rgba(51, 51, 51, 0.26);
    display: flex;
    justify-content: center;
    align-items: center;
}

.jumbotron {
    padding: 200px;
    height: 100vh;
    z-index: 1;
  
}

.display-4 {
    color: black;
   
    font-family: 'WindSong', cursive !important;
    
}
.lead {
    color: rgb(0, 0, 0);
    line-height: 40px;
    color: white;
    letter-spacing: 1.6px;
  


}
#exploreBtn {
 
    background-color: white;
    color: black;
    border-color: white;

    
}
@media(max-width: 900px) {

    .jumbotron {
        position: relative;
        top: 100px;
        padding: 0 10px;
    }
    
    .lead {
        font-size: .9rem;
    }
}
form {
    padding: 200px 50px;
    height: 100vh;
    color: white;
}

.form-group {
    margin: 0px 0px 15px 0px;
}
.form-group label {
    margin: 5px 0;
}

#contactSubmit {
    background-color: white;
    color: #896DF6;
}
@media(min-width: 900px) {
    form {
        padding: 200px 300px;
    }

}
.bottomNavUl {
    width: 100%;
  
    display: flex;
    justify-content: space-between;
    padding: 20px 50px 0px 50px;
    list-style-type: none;
    
}
#footer {
    background-color: white;
 
    z-index: 3;
}


.bottomNavUl li a {
    color: #896DF6;
    text-decoration: none;
    font-size: 1.2rem;
}
.bottomNavUl li {
    height: 100%;
}

@media(max-width: 700px) {
    .bottomNavUl {
        flex-direction: column;
        align-items: center;
    
        
    }
    .bottomNavUl li a {
        font-size: .9rem;
    }
}

#brand {
    font-size: 2.5rem;
    color: #896DF6;
    margin: 0;
}

.active {
    color: #C621E5 !important;
  
}

@media(min-width: 1000px) {
    .navbar-expand-lg {
        padding: 20px 100px;
    }

}
.gallery {
    display: grid;
    grid-template-columns: repeat(2, 1fr);

    width: 100%;
    height: auto;
    justify-items: center;
}

.project-card {
    width: 50%;
    height: auto;
    margin: 20px;
    border: solid thin white;
    border-radius: 5px;
    padding: 20px;
    background-color: white;
}

.project-card:hover {
    padding: 5px;
    border: solid 3px white;
}
.cardImg {
    width: 100%;
}
.gallery {
    padding: 100px 70px;
    margin: 25px 0px;
    text-align: center;
  }
  
.card-buttons {
    margin: 10px 0;
    
}
.cardBtn {
    font-size: 12px;
    margin: 8px;
    padding: 4px 8px;

}
#viewProjectBtn {
    background-color: #C621E5;
    border-color: transparent;


}
#viewProjectBtn a {
    color: white;
}

#viewCodeBtn {
    background-color: transparent;
    border: 1px solid #896DF6;
    

}
#viewCodeBtn a {
    color: #896DF6;

}
.cardBtn a {
    text-decoration: none;
}
@media (max-width: 1100px) {

    .gallery {
        grid-template-columns: repeat(1, 1fr);
        padding: 200px 30px;

    }
    .project-card {
        width: 70%;
    }
    .cardBtn {
        font-size: 10px;
        margin: 5px;
    }

    
}
.container {
  
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.container a {
    font-size: 3.5rem;
    color: white;
    

}

.container a:hover {
    color: black;
}

@media(max-width: 760px) {
    .container a {
        font-size: 1.5rem;
   
        
    
    }

}
